.container {
  min-height: 100vh;
  padding: 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.content {
  max-width: 1200px;
  margin: 0 auto;
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.title {
  text-align: center;
  font-size: 3rem;
  font-weight: 700;
  color: #1a202c;
  margin: 0;
  padding: 3rem 2rem 1rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.subtitle {
  text-align: center;
  font-size: 1.25rem;
  color: #718096;
  margin: 0 0 3rem;
  padding: 0 2rem 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: rgba(255, 255, 255, 0.9);
}

.contactGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0;
  min-height: 600px;
}

.contactInfo {
  background: #f7fafc;
  padding: 3rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.sectionTitle {
  font-size: 1.5rem;
  font-weight: 600;
  color: #2d3748;
  margin: 0 0 2rem;
}

.infoItem {
  margin-bottom: 2rem;
}

.infoItem h3 {
  font-size: 1.1rem;
  font-weight: 600;
  color: #4a5568;
  margin: 0 0 0.5rem;
}

.infoItem p {
  font-size: 1rem;
  color: #718096;
  margin: 0;
  line-height: 1.6;
}

.contactForm {
  padding: 3rem 2rem;
  background: white;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.formGroup label {
  font-size: 0.9rem;
  font-weight: 500;
  color: #4a5568;
}

.input,
.textarea {
  padding: 0.75rem 1rem;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.2s ease;
  background: white;
}

.input:focus,
.textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.textarea {
  resize: vertical;
  min-height: 120px;
}

.submitButton {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-top: 1rem;
}

.submitButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

.submitButton:active {
  transform: translateY(0);
}

/* Responsive design */
@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }

  .title {
    font-size: 2rem;
    padding: 2rem 1rem 1rem;
  }

  .subtitle {
    font-size: 1rem;
    padding: 0 1rem 1.5rem;
  }

  .contactGrid {
    grid-template-columns: 1fr;
  }

  .contactInfo,
  .contactForm {
    padding: 2rem 1.5rem;
  }
}